.btn{
    height: 40px;
    border-radius: 100px;
    width: 256px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-size: 12px;
    text-transform: uppercase;
    cursor: pointer;
}

.btn--default{
    background-color: rgba(255,255,255,0.65);
    color: black;
}

.btn--primary{
    background-color: rgba(23, 26, 32, 0.8);
    color: white;
}